import "./styles.css";
import { useState } from "react";

export default function App() {
  const [urls, setUrls] = useState();

  const createURL = (data) => {
    const whois = `https://who.is/whois/${data}`;
    const whatcms = `https://whatcms.org/?s=${data}`;
    const github = `https://github.com/search?q=${data}`;
    const googlesubbulma = `https://google.com/search?q=site:*.${data}`;
    const spysesubbulma = `https://spyse.com/target/domain/${data}/subdomain-list`;
    const hackertargetsub = `https://api.hackertarget.com/hostsearch/?q=${data}`;
    const shodan = `https://www.shodan.io/search?query=${data}`;
    const sitedetay = `https://builtwith.com/${data}`;
    const dijitalparmakizi = `https://spyse.com/search?query=${data}&target=domain`;
    const alexa = `https://www.alexa.com/siteinfo/${data}`;
    const sitemap = `http://www.visualsitemapper.com/map/${data}`;
    const zone = `https://api.hackertarget.com/zonetransfer/?q=${data}`;
    const httpheaders = `https://api.hackertarget.com/httpheaders/?q=${data}`;
    const pagelinks = `https://api.hackertarget.com/pagelinks/?q=${data}`;

    return [
      {
        key: "Whois",
        value: whois
      },
      {
        key: "Hangi CMS Kallanıyor?",
        value: whatcms
      },
      {
        key: "GitHub",
        value: github
      },
      {
        key: "Subdomain Bulma",
        value: googlesubbulma
      },
      {
        key: "Subdomain Bulma 2",
        value: spysesubbulma
      },
      {
        key: "Subdomain Bulma 3",
        value: hackertargetsub
      },
      {
        key: "Shodan",
        value: shodan
      },
      {
        key: "Web sitenin neler kullandığını öğrenin",
        value: sitedetay
      },
      {
        key: "Web siteni dijital izleri",
        value: dijitalparmakizi
      },
      {
        key: "Alexa",
        value: alexa
      },
      {
        key: "Site Haritası",
        value: sitemap
      },
      {
        key: "Zone Transfer",
        value: zone
      },
      {
        key: "HTTP Headers",
        value: httpheaders
      },
      {
        key: "Sayfa Bağlantıları",
        value: pagelinks
      }
    ];
  };

  const handleData = (e) => setUrls(createURL(e.target.value || ""));

  const renderLink = (title, data) => (
    <a key={data} id="custom-link" href={data} rel="noreferrer" target="_blank">
      {title}
    </a>
  );

  const renderLinkGroup = () =>
    urls?.map(({ key, value }) => renderLink(key, value));

  return (
    <div className="App">
      <a href="emreylmz.com" target="_blank">
        <img
          src="https://emreylmz.com/wp-content/uploads/2021/10/emreylmz_beyaz_logo.png"
          alt="emreylmz.com logo"
        />
      </a>

      <input type="text" placeholder="example.com" onChange={handleData} />
      {renderLinkGroup()}
    </div>
  );
}
